.cursor-pointer {
  cursor: pointer !important;
}

.bl-min-w-25 {
  min-width: 25% !important;
}

.bl-main-card-height {
  padding-bottom: 25px;
  height: 100%;
}

.bl-main-card-height > .card {
  height: 100%;
  margin-bottom: 0;
}

.bl-min-w60 {
  min-width: 60px !important;
}

.bl-min-w200 {
  min-width: 200px !important;
}

.textarea-animated {
  -webkit-transition: height 0.2s;
  -moz-transition: height 0.2s;
  transition: height 0.2s;
}

.bl-post-cats {
  max-height: 170px;
  overflow: hidden;
  position: relative;
}

.bl-post-col-tools {
  border-left: 1px #ddd solid;
}

.post-tags-most-item {
  margin-bottom: 3px;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.post-tags-most-item-sort:empty {
  display: inline-block;
}

.post-tags-most-item-sort:before {
  content: "-";
  visibility: hidden;
  opacity: 0;
}

.bl-list-tags {
  display: block;
  margin: 0;
  padding: 0;
}

.bl-list-tags li {
  display: inline-block;
  margin-right: 2px;
}

/* POSTING */
.blmd-header {
  display: flex;
  align-items: center;
}

.blmd-header .blmd-header-btns {
  flex-grow: 1;
  flex-shrink: 1;
}

.blmd-header .blmd-header-btns .btns-inner {
  display: flex;
  justify-content: flex-end;
}

.blmd-header .blmd-header-btns .btns-inner a {
  color: #586069;
}

.blmd-header .blmd-header-btns .btns-inner a:hover {
  color: #4285f4;
}

.blmd-editor {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

#post-markdown-preview {
  border: 1px #bdc0c7 solid;
  padding: 20px;
}

